    #t_3_5.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 3.5 Clasificacin de las prcticas agropecuarias sostenibles
    
    .row.mb-5
      .col-lg-5.order-lg-2.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/25.png", data-aos="zoom-in")
      .col-lg-7.order-lg-1
        .bg-color-5.tarjeta.p-5(data-aos="fade-left")
          p.mb-0 Las prcticas agropecuarias sostenibles pueden organizarse de distintas maneras, dependiendo del enfoque utilizado: ambiental, social, econmico o tcnico. No obstante, una forma prctica y pedaggica de clasificarlas es segn el tipo de recurso natural o funcin ecosistmica que buscan conservar o mejorar.
    
    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Prcticas de conservacin del suelo
    
    p.mb-5 El suelo es un recurso vital para la produccin de alimentos y un componente clave de los ecosistemas terrestres. Se forma lentamente a lo largo de millones de aos mediante procesos naturales como la descomposicin de rocas, la accin de microorganismos, el clima y el paso del tiempo (Instituto Geogrfico Agustn Codazzi, 2008). Dado que su regeneracin es extremadamente lenta, su prdida por erosin, compactacin o degradacin qumica representa una amenaza seria para la seguridad alimentaria y la sostenibilidad agrcola.

    .row.mb-5
      .col-lg-5.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/26.png", data-aos="zoom-in")
      .col-lg-7
        .bg-color-25.tarjeta.p-4.mb-3(data-aos="fade-left")
          h5 Qu buscan las prcticas de conservacin del suelo?
