          div
            ul.lista-ul--color.mb-0
              li.mb-2.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Evitar la prdida de suelo frtil por erosin hdrica o elica.
              li.mb-2.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Conservar y mejorar su estructura, aireacin, humedad y fertilidad.
              li.mb-2.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Mantener la actividad biolgica del suelo, favoreciendo los microorganismos y la materia orgnica
    
    p.mb-5 Los ejemplos de prcticas sostenibles para conservar el suelo son:

    .bg-full-width.bg-fondo-slider.mb-5
      .p-4.p-md-5
        SlyderA(tipo="b").bg-color-white-1.p-4
