              p Sembrar varias especies en el mismo terreno, lo que mejora el uso del espacio y protege el suelo.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/29.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Labranza mnima o siembra directa
              p Evitar el arado profundo para preservar la estructura del suelo y reducir su compactacin.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/30.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Cobertura vegetal (cultivos de cobertura y abonos verdes)
              p Sembrar plantas que cubren el suelo cuando no hay cultivo principal, protegindolo de la erosin y enriquecindolo orgnicamente.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/31.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Terrazas en laderas y zanjas de escorrenta
              p Modificar el relieve del terreno para frenar la erosin en zonas inclinadas y conservar el agua.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/32.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Uso de abonos orgnicos o compost
              p Reemplazar fertilizantes sintticos por materiales orgnicos que mejoran la fertilidad y activan la vida del suelo.
    
    p.mb-5 La conservacin del suelo es una prioridad estratgica en la agricultura sostenible. Su degradacin afecta directamente la productividad, la calidad de los alimentos y el equilibrio ecolgico. Adoptar prcticas que lo protejan es un paso esencial para garantizar sistemas productivos duraderos y responsables con el ambiente.

    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Prcticas de manejo eficiente del agua
    
    .bloque-texto-g.color-acento-contenido.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema3/33.png')})`}"
      )(data-aos="zoom-in")
      .bloque-texto-g__texto.p-4
        p.mb-0 El agua es uno de los recursos naturales ms valiosos y limitados del planeta. Aunque abunda en la Tierra, solo una pequea fraccin es dulce y accesible, y esta se encuentra distribuida de forma desigual. Por eso, su gestin sostenible es esencial para garantizar la seguridad alimentaria, la sostenibilidad ambiental y la continuidad de la produccin agropecuaria. En este contexto, el manejo eficiente del agua es una de las principales prioridades dentro de las prcticas agropecuarias sostenibles.
    
    h3.bg-color-21.d-flex.d-xl-inline.p-3.text-white.tarjeta Por qu es importante el uso eficiente del agua?

    p.mb-5.mt-5 Las prcticas agropecuarias requieren grandes cantidades de agua, tanto para el riego de cultivos como para el consumo animal y el procesamiento de productos. Implementar estrategias que reduzcan el desperdicio y protejan las fuentes hdricas permite:
    
    .row.mb-5
      .col-lg-4.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/34.png", data-aos="zoom-in")
      .col-lg-8
        .bg-color-1.tarjeta.p-4.mb-3(data-aos="fade-left")
          div
            ul.lista-ul--color.mb-0
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Aumentar la eficiencia del riego y del consumo.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Evitar la sobreexplotacin de acuferos y nacimientos.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Prevenir la contaminacin de ros, quebradas y lagos.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Adaptarse mejor a la variabilidad climtica (sequas prolongadas, lluvias extremas).

    p.mb-5 Las principales prcticas de manejo eficiente del agua son: 

    .row.mb-5
      .col-lg-4.mb-3.mb-lg-0.order-lg-2
        figure
          img(src="@/assets/curso/tema3/35.png", data-aos="zoom-in")
      .col-lg-8.order-lg-1
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-color-4")
          div(titulo="Riego tecnificado (goteo o microaspersin)").ms-5
            p.mb-0 Aplica el agua directamente en la zona radicular. 
            p.mb-0 Reduce la evaporacin y las prdidas por escurrimiento. 
            p.mb-0 Puede ahorrar hasta un 60 % ms de agua frente a mtodos tradicionales.


          div(titulo="Captacin y almacenamiento de agua lluvia").ms-5
            p.mb-0 Recoleccin de agua durante la temporada de lluvias en tanques o reservorios para usarla en poca seca. 
            p.mb-0 Favorece la autosuficiencia hdrica de las fincas.

          div(titulo="Programacin del riego segn necesidades del cultivo").ms-5
            p.mb-0 Uso de sensores de humedad y calendarios agronmicos para determinar cundo y cunto regar. 
            p.mb-0 Evita el exceso de riego y mejora el rendimiento.
 
          div(titulo="Manejo del suelo para retencin de humedad").ms-5
            p.mb-0 Uso de mulch (acolchado), labranza mnima y cultivos de cobertura para reducir la evaporacin y mejorar la infiltracin.
    
    .row.mb-5
      .col-lg-4.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/36.png", data-aos="zoom-in")
      .col-lg-8
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-color-4")
          div(titulo="Reutilizacin de aguas residuales tratadas").ms-5
            p.mb-0 Empleo de aguas grises adecuadamente tratadas para riego o uso secundario. 
            p.mb-0 Requiere control sanitario estricto para evitar contaminacin.


          div(titulo="Proteccin de fuentes de agua").ms-5
            p.mb-0 Reforestacin de riberas, cercado de nacimientos y control de contaminantes.  
            p.mb-0 Evita el ingreso de ganado y residuos a las fuentes hdricas.

          div(titulo="Manejo del agua en sistemas ganaderos").ms-5
            p.mb-0 Instalacin de bebederos eficientes, uso de agua lluvia para animales y tratamiento adecuado de aguas residuales.
 
          div(titulo="Construccin de zanjas de infiltracin y terrazas").ms-5
            p.mb-0 Permiten que el agua se infiltre en zonas de pendiente, reducen la erosin y recargan acuferos subterrneos.
    
    p.mb-5 El manejo eficiente del agua no solo optimiza el uso del recurso, sino que tambin fortalece la resiliencia del sistema productivo frente al cambio climtico, mejora el rendimiento de los cultivos, reduce costos y protege los ecosistemas hdricos locales. Estas prcticas son fundamentales en cualquier enfoque de produccin agropecuaria sostenible.

    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Prcticas para conservar la biodiversidad

    .row.mb-5.align-items-center
      .col-lg-5.order-lg-2.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/37.png", data-aos="zoom-in")
      .col-lg-7.order-lg-1
        .bg-color-15.tarjeta.p-5(data-aos="fade-left")
          p.mb-0 Antes de implementar cualquier estrategia de sostenibilidad, es fundamental comprender qu se entiende por biodiversidad y por qu es esencial conservarla en los sistemas agropecuarios.
          
    h3.bg-color-21.d-flex.d-xl-inline.p-3.text-white.tarjeta Qu es la biodiversidad?

    p.mb-5.mt-5 La biodiversidad abarca toda la variedad de formas de vida presentes en la naturaleza, desde los genes hasta los ecosistemas completos. Su conservacin es clave para mantener el equilibrio ecolgico y garantizar la sostenibilidad de la produccin.

    .row.mb-5.align-items-center
      .col-lg-5.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/38.png", data-aos="zoom-in")
      .col-lg-7
        p(data-aos="fade-left") Segn el Convenio sobre la Diversidad Biolgica (1992), la biodiversidad es:
        .bloque-texto-b.color-primario.p-4
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            h2.mb-0 La variabilidad de organismos vivos de cualquier fuente, incluidos, entre otros, los ecosistemas terrestres, marinos y otros ecosistemas acuticos, y los complejos ecolgicos de los que forman parte; comprende la diversidad dentro de cada especie, entre especies y de los ecosistemas.
            i.fas.fa-quote-right
    
    h3.bg-color-21.d-flex.d-xl-inline.p-3.text-white.tarjeta Cules son los componentes de la biodiversidad?

    p.mb-5.mt-5 La biodiversidad se manifiesta en distintos niveles, cada uno fundamental para el funcionamiento de los ecosistemas y la sostenibilidad agropecuaria.

    .row.mb-5.justify-content-center
      .col-sm-6.col-lg-3.mb-4.mb-lg-0(data-aos="fade-down")
        .tarjeta--boton.bg-color-24.p-4.h-100
          .row.justify-content-center.mb-3
            .col-7
              figure
                img(src='@/assets/curso/tema3/40.svg', alt='Texto que describa la imagen')
          div.text-center
            h4 Diversidad gentica
            p Variabilidad de genes dentro de una misma especie.
      .col-sm-6.col-lg-3.mb-4.mb-lg-0(data-aos="fade-up")
        .tarjeta--boton.bg-color-24.p-4.h-100
          .row.justify-content-center.mb-3
            .col-7
              figure
                img(src='@/assets/curso/tema3/41.svg', alt='Texto que describa la imagen')
          div.text-center
            h4 Diversidad de especies
            p Nmero y variedad de especies (animales, plantas, hongos, microorganismos).
      .col-sm-6.col-lg-3.mb-4.mb-sm-0(data-aos="fade-down")
        .tarjeta--boton.bg-color-24.p-4.h-100
          .row.justify-content-center.mb-3
            .col-7
              figure
                img(src='@/assets/curso/tema3/42.svg', alt='Texto que describa la imagen')
          div.text-center
            h4 Diversidad de ecosistemas
            p Conjunto de elementos vivos y no vivos que interactan en un mismo entorno natural.

      .col-sm-6.col-lg-3.mb-4.mb-sm-0(data-aos="fade-down")
        figure
          img(src='@/assets/curso/tema3/39.png', alt='Texto que describa la imagen')

    h3.bg-color-21.d-flex.d-lg-inline.p-3.text-white.tarjeta Cmo contribuyen las prcticas agropecuarias sostenibles a conservar la biodiversidad?

    p.mb-5.mt-5 Las prcticas sostenibles aplicadas en el campo pueden ayudar a mantener o incluso mejorar la biodiversidad del entorno. A continuacin, se presentan las ms relevantes.

    .bg-full-width.bg-fondo-slider.mb-5
      .p-4.p-md-5
        SlyderA(tipo="b").bg-color-white-1.p-4
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/43.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Agroforestera
              p Combina rboles con cultivos o pastos, generando hbitats diversos y beneficios ecolgicos como sombra, proteccin del suelo y atraccin de fauna.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/44.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Corredores biolgicos
              p Conectan fragmentos de hbitats para permitir el movimiento de especies silvestres. Previenen el aislamiento poblacional.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/45.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Diversificacin de cultivos y rotacin
              p Promueve diferentes hbitats, reduce plagas y mejora la salud del suelo.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/46.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Uso de insumos orgnicos y manejo ecolgico
              p Disminuye el uso de qumicos, protege organismos benficos y aumenta la biodiversidad microbiana.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/47.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Mantenimiento de reas naturales
              p Conservar zonas con vegetacin nativa protege especies silvestres y mantiene servicios ecosistmicos.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/48.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Conservacin del suelo
              p Prcticas como la labranza mnima y el mantillo mejoran la vida del suelo y la diversidad de organismos que lo habitan.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/49.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Apicultura
              p Las abejas polinizan cultivos y plantas silvestres, favoreciendo la biodiversidad vegetal. Es una prctica rentable y de bajo impacto.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/50.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Uso de especies nativas
              p Favorece el equilibrio ecolgico al evitar el reemplazo de especies locales por exticas o invasoras.
    
    h3.bg-color-21.d-flex.d-xl-inline.p-3.text-white.tarjeta Por qu destacar la apicultura dentro de estas prcticas?

    p.mb-5.mt-5 La apicultura es una de las actividades agropecuarias ms sostenibles, y su importancia va ms all de la produccin de miel.

    .row.mb-5
      .col-lg-5.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/51.png", data-aos="zoom-in")
      .col-lg-7
        .bg-color-11.tarjeta.p-4.mb-3(data-aos="fade-left")
          div
            ul.lista-ul--color.mb-0
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Las abejas polinizan una gran variedad de cultivos y plantas silvestres.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Contribuyen al equilibrio de los ecosistemas sin generar contaminacin.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Representan una fuente de ingreso para pequeos productores.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Ayudan a reducir el uso de pesticidas mediante el control biolgico.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Promueven la educacin ambiental y la proteccin de polinizadores.
    
    p Conservar la biodiversidad no es solo una responsabilidad ecolgica, sino tambin una estrategia inteligente para garantizar una produccin agropecuaria ms estable, resiliente y saludable.
    p.mb-5 Las prcticas que la favorecen son clave en los sistemas sostenibles y deben ser adoptadas de forma integral y adaptada al contexto local.

    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Prcticas ganaderas sostenibles
    
    p.mb-5 Las prcticas ganaderas sostenibles buscan equilibrar tres aspectos fundamentales:

    .row.justify-content-center
      .col-lg-3.d-flex
        .bg-color-28.mb-3.tarjeta.flex-fill.d-flex.align-items-center.justify-content-center
          .px-4.py-4.text-center.text-white
            p.mb-0 La produccin animal eficiente.
      .col-lg-3.d-flex
        .bg-color-28.mb-3.tarjeta.flex-fill.d-flex.align-items-center.justify-content-center
          .px-4.py-4.text-center.text-white
            p.mb-0 El bienestar econmico y tico del productor.
      .col-lg-3.d-flex
        .bg-color-28.mb-3.tarjeta.flex-fill.d-flex.align-items-center.justify-content-center
          .px-4.py-4.text-center.text-white
            p.mb-0 La produccin animal eficiente.
    
    p.mb-5 A continuacin, se describen las principales prcticas aplicadas en sistemas ganaderos sostenibles:

    .row.mb-5
      .col-lg-4.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/52.png", data-aos="zoom-in")
      .col-lg-8
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-color-4")
          div(titulo="Manejo racional de pastos (pastoreo rotativo)").ms-5
            p Consiste en dividir los potreros en sectores o parcelas y trasladar el ganado de manera planificada. Esto permite que los pastos se recuperen antes de ser utilizados nuevamente.  
            h5 Beneficios: 
            ul.lista-ul--color.mb-0.ms-3
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Se evita el sobrepastoreo. 
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Regeneracin natural de los pastos. 
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Mejora la fertilidad del suelo y reduce la erosin.
          div(titulo="Silvopastoreo").ms-5
            p Es un sistema agropecuario que integra rboles, pastos y animales de manera planificada. 
            h5 Beneficios clave del silvopastoreo: 
            ul.lista-ul--color.mb-0.ms-3
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Salud del suelo: mejora la fertilidad y reduce la erosin; algunas especies fijan nitrgeno.  
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Bienestar animal: brinda sombra y proteccin, reduce el estrs trmico y mejora la productividad. 
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Diversificacin productiva: produce carne, leche, miel, lea, madera, frutas, etc., diversificando ingresos. 
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Biodiversidad: aumenta la presencia de fauna til, que contribuye al control de plagas.   
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Conservacin del agua: favorece la infiltracin del agua y protege las fuentes hdricas.  
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Captura de carbono: los rboles almacenan CO, ayudando a mitigar el cambio climtico. 
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Reduccin de costos: menor uso de suplementos, fertilizantes y medicamentos.
            h5 Ejemplos de especies empleadas: 
            ul.lista-ul--color.mb-0.ms-3
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 rboles forrajeros: Leucaena leucocephala, Gliricidia sepium, Prosopis spp.   
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Maderables y frutales: Cedro, Caoba, Guayaba, Nance. 
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Pastos nutritivos: Brachiaria, Panicum, Estrella africana. 

          div(titulo="Manejo adecuado de estircoles y residuos").ms-5
            p Consiste en el aprovechamiento de desechos animales como fertilizantes orgnicos.
            h3 Beneficios:  
            ul.lista-ul--color.mb-0.ms-3
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Reduce la contaminacin del suelo y del agua.   
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Genera energa renovable mediante la produccin de biogs. 
 
          div(titulo="Alimentacin balanceada y eficiente").ms-5
            p Se basa en el uso de forrajes locales, suplementos naturales y residuos agrcolas, en lugar de concentrados comerciales.
            h5 Ventajas: 
            ul.lista-ul--color.mb-0.ms-3
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Menor huella de carbono.    
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Reduccin de costos de produccin.
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Mejora la eficiencia digestiva.    
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Disminuye la emisin de gases como el metano por cada kg de alimento.
          div(titulo="Bienestar animal").ms-5
            p Implica garantizar condiciones dignas y saludables para los animales, incluyendo:  Alimentacin y agua constante. 
            ul.lista-ul--color.mb-0.ms-3
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Espacio suficiente para movilizarse.   
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Proteccin frente al clima. 
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Atencin veterinaria adecuada.     
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Prevencin del maltrato y prcticas dolorosas (como mutilaciones innecesarias). 
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Evitar mejoramientos genticos que comprometan la salud. Adems, se promueve el uso de razas criollas o autctonas, adaptadas al entorno local y ms resilientes. 
            p <b>Nota</b>: antes de iniciar una produccin sostenible, es fundamental formarse y certificarse en Buenas Prcticas Ganaderas (BPG) segn la norma nacional correspondiente.

    .row.mb-5
      .col-lg-4.mb-3.mb-lg-0.order-lg-2
        figure
          img(src="@/assets/curso/tema3/53.png", data-aos="zoom-in")
      .col-lg-8.order-lg-1
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-color-4")
          div(titulo="Uso racional del agua").ms-5
            p Se promueve:
            ul.lista-ul--color.mb-0.ms-3
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Instalacin de bebederos eficientes. 
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Cosecha de agua lluvia. 
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Limpieza eficiente de establos.
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Evitar que el ganado acceda libremente a fuentes hdricas para prevenir su deterioro.
          div(titulo="Integracin agricultura - ganadera").ms-5
            p Este enfoque aprovecha los residuos de cada sistema para alimentar al otro: 
            ul.lista-ul--color.mb-0.ms-3
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Residuos agrcolas  alimento animal. 
