              p <b>Resultado</b>: mayor eficiencia y menor dependencia de insumos externos.
            
          div(titulo="Reduccin del uso de antibiticos y qumicos").ms-5
            p La ganadera sostenible promueve la prevencin sobre el tratamiento, reduciendo el uso de medicamentos. 
            h3 Razones clave:  
            ul.lista-ul--color.mb-0.ms-3
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Salud pblica: evita la aparicin de bacterias resistentes a antibiticos.    
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Medio ambiente: reduce la contaminacin de suelos, agua y aire por residuos qumicos.  
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Bienestar animal: se prioriza el manejo adecuado, higiene, nutricin y reduccin del estrs, disminuyendo la necesidad de medicamentos. 
 
          div(titulo="Ganadera regenerativa y ecolgica").ms-5
            p Ambos enfoques comparten principios de sostenibilidad, pero tienen objetivos y regulaciones distintas.  
            ul.lista-ul--color.mb-0.ms-3
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 <b>Ganadera regenerativa:</b> su objetivo es regenerar suelos y ecosistemas. Su enfoque es holstico y va ms all de la sostenibilidad. Sus prcticas incluyen pastoreo racional, aumento de biodiversidad, captura de carbono, sin agroqumicos. En cuanto al bienestar animal, busca su integracin natural al ecosistema.    
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 <b>Ganadera ecolgica (Orgnica):</b> su objetivo es producir alimentos sanos sin daar el ambiente. Est regulada por normas internacionales (UE, USDA). Sus prcticas se basan en el no uso de qumicos sintticos ni transgnicos, alimentacin orgnica y control sanitario natural. En el bienestar animal garantiza condiciones ticas: acceso a pastos, sin confinamiento y cuidado integral.
          div(titulo="Educacin y capacitacin continua").ms-5
            p Consiste en capacitar constantemente a los campesinos en temas como: 
            ul.lista-ul--color.mb-0.ms-3
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Buenas Prcticas Ganaderas (BPG).    
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Crianza respetuosa. 
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Gestin de riesgos. Esto representa un valor agregado fundamental para: 
