                i.fas.fa-leaf.bg-color-7
                p.mb-0 Elevar la productividad.
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Mitigar los efectos del cambio climtico. 
              li.mb-3.d-flex
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Cumplir con estndares valorados en mercados especializados.
    
