        h3.titulo-con-imagen__texto Manejo ecolgico de plagas y enfermedades y BPA
    
    p.mb-5 El manejo ecolgico de plagas y enfermedades es un enfoque preventivo, sostenible y respetuoso con el medio ambiente que busca controlar plagas, enfermedades y malezas que afectan los cultivos o animales sin recurrir o recurriendo mnimamente a agroqumicos sintticos. Este enfoque prioriza prcticas naturales, culturales y biolgicas, procurando:

