          .col-lg-6.mb-3.mb-lg-0
            .bg-color-9.h-100.p-4.tarjeta.text-center(data-aos="fade-left")
              p.mb-0.text-bold Mantener el equilibrio ecolgico.
        .row.mb-3
          .col-lg-6.mb-3.mb-lg-0
            .bg-color-9.h-100.p-4.tarjeta.text-center(data-aos="fade-left")
              p.mb-0.text-bold Reducir el dao econmico.
          .col-lg-6.mb-3.mb-lg-0
            .bg-color-9.h-100.p-4.tarjeta.text-center(data-aos="fade-left")
              p.mb-0.text-bold Proteger la salud humana, animal y ambiental.
    
    p.mb-5 Los principales componentes del manejo ecolgico son: 

    .bg-full-width.bg-fondo-slider.mb-5
      .p-4.p-md-5
        SlyderA(tipo="b").bg-color-white-1.p-4
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/55.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Prevencin y monitoreo
              p Uso de semillas sanas y resistentes, rotacin y diversificacin de cultivos, monitoreo constante con trampas e inspecciones peridicas.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/56.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Control cultural
              p Siembras en fechas adecuadas, labores oportunas (deshierbe, poda, riego), eliminacin de residuos infectados, y manejo del suelo para fortalecer plantas y animales.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/57.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Control biolgico
              p Empleo de enemigos naturales como insectos benficos, hongos o bacterias. Ejemplos: mariquitas (pulgones), Trichogramma (avispa parasitoide), Bacillus thuringiensis (Bt).
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/58.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Control fsico o mecnico
              p Trampas de luz, trampas con feromonas, mallas, barreras fsicas, o remocin manual de plagas.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/59.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Uso limitado de insumos naturales
              p Extractos vegetales (ajo, neem, ruda), jabones potsicos, aceites vegetales, y otros productos aprobados por normativas ecolgicas.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/60.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Capacitacin y conocimiento del agroecosistema
              p Comprender las relaciones entre suelo, clima, plagas, enemigos naturales, y tomar decisiones basadas en el anlisis del entorno y no solo en sntomas visibles.
    
    .bg-color-5.tarjeta.mb-5
