          .col-lg
            p.mb-0 <b>Importante</b>. El objetivo no es eliminar totalmente las plagas, sino mantenerlas en niveles que no causen daos significativos, sin alterar el equilibrio del ecosistema.

    h3.bg-color-21.d-flex.d-xl-inline.p-3.text-white.tarjeta Buenas Prcticas Agrcolas (BPA)

    p.mb-5.mt-5 Las BPA son un conjunto de principios, normas y recomendaciones tcnicas aplicadas durante la produccin, cosecha y poscosecha de productos agrcolas. Su fin es garantizar:

