            p Integrar rboles con cultivos agrcolas, lo cual mejora la estructura del suelo, regula microclimas y aporta otros productos (frutas, lea, sombra, etc.).
      .col-sm-6.col-lg-3.mb-4.mb-sm-0(data-aos="fade-down")
