        h3.titulo-con-imagen__texto Prcticas de economa circular y reciclaje
    
    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema3/68.png')})`}"
      )(data-aos="zoom-in")
      .bloque-texto-g__texto.p-4
        p.mb-0 En la bsqueda de sistemas productivos ms sostenibles, la economa circular y el reciclaje emergen como prcticas clave para reducir residuos, aprovechar mejor los recursos disponibles y optimizar la eficiencia en el sector agropecuario. Aunque estn estrechamente relacionadas, no significan lo mismo, como se explicar a continuacin.
    
    h3.bg-color-21.d-flex.d-xl-inline.p-3.text-white.tarjeta Qu es la economa circular?

    .row.mb-5.mt-5
      .col-lg-5.order-lg-2.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/69.png", data-aos="zoom-in")
      .col-lg-7.order-lg-1
        p(data-aos="fade-left") Antes de revisar las aplicaciones concretas, es fundamental comprender qu es la economa circular. Se trata de un modelo econmico que propone minimizar el desperdicio y maximizar el aprovechamiento de los recursos. A diferencia de la economa lineal tradicional (extraer, producir, consumir, desechar), la economa circular plantea:
        .bg-color-30.tarjeta.p-4.mb-3(data-aos="fade-left")
          div
            ul.lista-ul--color.mb-0
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Disear productos duraderos, fciles de reparar y reciclar.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Reutilizar y reparar antes de desechar.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Reciclar materiales para que vuelvan al ciclo productivo.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Reducir el uso de materias primas y energa.
        p El objetivo es construir un sistema ms sostenible, donde los recursos se mantengan en uso el mayor tiempo posible, generando eficiencia, ahorro y menor impacto ambiental.

    h3.bg-color-21.d-flex.d-xl-inline.p-3.text-white.tarjeta Aplicaciones de economa circular en el sector agropecuario

    p.mb-5.mt-5 Este modelo econmico se traduce en una serie de acciones concretas dentro del contexto agropecuario, que permiten transformar residuos en recursos valiosos. A continuacin, se presentan algunas de las prcticas ms relevantes:

    .row.mb-3
      .col-lg-4.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/70.png", data-aos="zoom-in")
      .col-lg-8
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-color-4")
          div(titulo="Reutilizacin de residuos orgnicos").ms-5
            p El estircol se usa como fertilizante natural y los restos de cosechas se compostan para enriquecer el suelo.
          div(titulo="Biodigestores").ms-5
            p Transforman residuos orgnicos en biogs (energa renovable) y biofertilizantes.
          div(titulo="Rotacin y asociacin de cultivos").ms-5
            p Evita el desgaste del suelo, mejora la biodiversidad y reduce el uso de agroqumicos.
          div(titulo="Agua reciclada").ms-5
            p Sistemas de riego que reutilizan agua de lluvia o agua residual tratada de procesos agroindustriales.
          div(titulo="Alimentacin animal circular").ms-5
            p Animales alimentados con subproductos agrcolas (como cscaras y restos de frutas).
          div(titulo="Produccin y consumo local").ms-5
            p Disminuye la huella de carbono al reducir distancias de transporte.
          div(titulo="Energas renovables").ms-5
            p Uso de tecnologas limpias como paneles solares y biodigestores en el entorno rural.

    h3.bg-color-21.d-flex.d-xl-inline.p-3.text-white.tarjeta Beneficios de la economa circular para el productor agropecuario

    p.mb-5.mt-5 Adems de proteger el medio ambiente, estas prcticas representan ventajas econmicas y sociales para quienes las implementan. Entre los principales beneficios se destacan:

    .row.mb-5
      .col-lg-5.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/71.png", data-aos="zoom-in")
      .col-lg-7
        .bg-color-9.tarjeta.p-4.mb-3(data-aos="fade-left")
          div
            ul.lista-ul--color.mb-0
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Ahorro en el uso de fertilizantes, energa y agua.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Menor dependencia de insumos externos.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Produccin con valor agregado y mayor aceptacin en mercados responsables.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Mejor imagen frente a consumidores que valoran la sostenibilidad.

    h3.bg-color-21.d-flex.d-xl-inline.p-3.text-white.tarjeta Qu es el reciclaje?

    .row.mb-5.mt-5
      .col-lg-6.order-lg-2.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/72.svg", data-aos="zoom-in")
      .col-lg-6.order-lg-1
        p(data-aos="fade-left") Dentro del enfoque de economa circular, el reciclaje es una de las estrategias ms conocidas y aplicadas. Consiste en recolectar materiales usados como plstico, papel, vidrio o metal, para transformarlos en nuevos productos o materias primas que puedan reincorporarse al proceso productivo.
        .bg-color-23.tarjeta.p-4.mb-3(data-aos="fade-left")
          p.mb-0 El objetivo principal del reciclaje es evitar que los residuos terminen contaminando el ambiente o en vertederos. Un ejemplo prctico en el sector agropecuario es la elaboracin de abonos orgnicos a partir de estircoles y desechos vegetales mediante tcnicas como el compostaje. Para comprender mejor cmo se relacionan, es til comparar estos dos conceptos en una tabla:
    
    .row.justify-content-center
      .col-lg-10
        .titulo-sexto.color-acento-contenido
          h5 Tabla 2.
          span Comparacin entre economa circular y reciclaje 
        
