    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Enfoque social y comunitario
    
    .row.mb-5.align-items-center
      .col-lg-5.order-lg-2.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/73.svg", data-aos="zoom-in")
      .col-lg-7.order-lg-1
        .bg-color-6.tarjeta.p-5(data-aos="fade-left")
          p.mb-0 La sostenibilidad agropecuaria no solo se basa en el cuidado ambiental o la eficiencia productiva. Tambin implica un compromiso social, donde el bienestar de las personas, la equidad y el fortalecimiento del tejido comunitario son elementos fundamentales.

    h3.bg-color-21.d-flex.d-xl-inline.p-3.text-white.tarjeta Qu significa tener un enfoque social y comunitario?

    p.mb-5.mt-5 Cuando se afirma que una prctica agropecuaria es sostenible desde el punto de vista social y comunitario, se est reconociendo que las personas y sus comunidades deben estar en el centro de las decisiones y beneficios del sistema productivo. Este enfoque busca integrar justicia social, participacin activa y respeto por la diversidad cultural. A continuacin, se describen los elementos clave que definen este enfoque:

    .bg-full-width.bg-fondo-slider.mb-5
      .p-4.p-md-5
        SlyderA(tipo="b").bg-color-white-1.p-4
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/74.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Bienestar de las personas
              p Se prioriza la calidad de vida de los agricultores, sus familias y comunidades rurales.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/75.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Equidad social
              p Se promueve el acceso justo a recursos y oportunidades para pequeos productores, mujeres, jvenes, comunidades indgenas y afrocolombianas.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/76.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Participacin colectiva
              p Las decisiones se toman de forma participativa, reconociendo y valorando el conocimiento local y ancestral.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/77.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Trabajo digno
              p Se generan empleos rurales con condiciones laborales justas, estables y seguras.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.mb-3.mb-lg-0.order-2
              figure
                img(src="@/assets/curso/tema3/78.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5 Identidad y cultura rural
              p Se protege y fortalece la vida campesina, las tradiciones, valores y saberes del campo.
    
    h3.bg-color-21.d-flex.d-xl-inline.p-3.text-white.tarjeta Por qu es importante este enfoque?

    p.mb-5.mt-5 Incluir un enfoque social y comunitario en la produccin agropecuaria es indispensable para lograr una sostenibilidad real y duradera. Las siguientes razones explican su relevancia:

    .row.mb-5.justify-content-center
      .col-lg-5.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/79.png", data-aos="zoom-in")
      .col-lg-7
        .bg-color-11.tarjeta.p-4.mb-3(data-aos="fade-left")
          div
            ul.lista-ul--color.mb-0
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Porque la sostenibilidad no puede limitarse al medio ambiente; tambin debe ser econmica y social.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Porque un campo justo, equitativo y participativo construye comunidades ms resilientes, fuertes y autnomas.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Porque muchos problemas ambientales estn directamente ligados a desigualdades sociales, como la pobreza, la migracin forzada y el acceso desigual a la tierra, el agua o la educacin.
        p.mb-0 Un enfoque social y comunitario en las prcticas agropecuarias sostenibles garantiza que la produccin de alimentos:
    
    .row.justify-content-center
      .col-lg-10
        .bg-color-5.tarjeta.mb-5
          .px-4.px-md-5.py-4
            .row.align-items-center
              .col-lg-auto.mb-3.mb-lg-0
                img(src="@/assets/curso/tema3/80.svg", style="max-width: 90px").mx-auto
              .col-lg
                p.mb-0 Sea buena para el planeta,
                p.mb-0 Pero tambin justa e inclusiva para las personas,
                p.mb-0 Y que fortalezca el bienestar, la equidad y la cultura en las comunidades rurales.
    
    separador
    
    #t_3_6.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 3.6	Alternativas de produccin sustentable para el sector rural

    .row.mb-5.mt-5
      .col-lg-5.order-lg-2.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/81.png", data-aos="zoom-in")
      .col-lg-7.order-lg-1
        p(data-aos="fade-left") En muchas regiones rurales, las actividades agropecuarias tradicionales no son suficientes para asegurar ingresos estables ni condiciones de vida dignas. Por ello, surgen diversas alternativas de produccin sustentable, que permiten generar recursos econmicos de forma ambientalmente responsable, econmicamente viable y socialmente justa.
        .bg-color-4.tarjeta.p-4.mb-3(data-aos="fade-left")
          p Estas alternativas son especialmente tiles en contextos como:
          div
            ul.lista-ul--color.mb-0
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Zonas donde la capacidad productiva del suelo es limitada.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 pocas del ao con bajos ingresos por falta de cosechas.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Regiones afectadas por el cambio climtico que requieren formas nuevas de produccin.
        p Adems, muchas de estas prcticas estn estrechamente ligadas a la cohesin social y el rescate de saberes ancestrales, aportando valor no solo econmico, sino tambin cultural y comunitario.

    .row.mb-5.mt-5.align-items-center
      .col-lg-5.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/82.png", data-aos="zoom-in")
      .col-lg-7
        p(data-aos="fade-left") Los criterios de sostenibilidad que deben cumplir estas alternativas son: 
        .bg-color-5.tarjeta.p-4.mb-3(data-aos="fade-left")
          div
            ul.lista-ul--color.mb-0
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Conservacin de recursos naturales (agua, suelo, biodiversidad).
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Reduccin de la contaminacin y el desperdicio.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Fortalecimiento de las comunidades rurales.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Mejora de la calidad de vida de las familias campesinas.
    
    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Principales alternativas sustentables
    
    p.mb-5 A continuacin, se describen algunas de las alternativas ms representativas. Es importante destacar que existen muchas otras prcticas que, aunque poco valoradas actualmente, pueden rescatarse y adaptarse para fortalecer la sostenibilidad rural.

    .row.mb-5
      .col-lg-4.mb-3.mb-lg-0.order-lg-2
        figure
          img(src="@/assets/curso/tema3/83.png", data-aos="zoom-in")
      .col-lg-8.order-lg-1
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-color-4")
          div(titulo="Biocomercio").ms-5
            p Conjunto de actividades de recoleccin, produccin, transformacin y comercializacin de productos y servicios derivados de la biodiversidad nativa. Principios: conserva los ecosistemas, respeta los conocimientos tradicionales, genera beneficios econmicos justos. Ejemplos: aceite de aguaje, jabones con plantas medicinales, cacao nativo.


          div(titulo="Especies promisorias (vegetales y pecuarias)").ms-5
            p Recursos biolgicos con alto potencial productivo, medicinal, ecolgico o nutricional an poco aprovechados. Ejemplos vegetales: moringa, quinua. Ejemplos pecuarias: cuy, peces nativos amaznicos.

          div(titulo="Rescate de tcnicas tradicionales").ms-5
            p Recuperacin de oficios y saberes que permiten dar valor agregado a materias primas y generar ingresos sustentables. Ejemplos: fique, cestera, sombreros artesanales.
    
    .row
      .col-lg-4.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/84.png", data-aos="zoom-in")
      .col-lg-8
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-color-4")
          div(titulo="Transformacin artesanal de productos agropecuarios").ms-5
            p Los campesinos transforman productos primarios usando saberes tradicionales, diferencindolos de los industriales y aumentando su valor. Ejemplos: quesos campesinos, arepas, almojbanas, amasijos de maz, derivados de la leche. Incluye ferias de gastronoma y artesana apoyadas por entidades pblicas.


          div(titulo="Pago por servicios ambientales (PSA)").ms-5
            p Mecanismo econmico voluntario para compensar a quienes conservan, restauran o protegen ecosistemas que generan beneficios colectivos (captura de carbono, regulacin hdrica, conservacin de biodiversidad). Ejemplo: pago a campesinos que conservan un bosque como sumidero de carbono.

          div(titulo="Artesana, cestera y cermica tradicional").ms-5
            p Elaboracin de productos con tcnicas tradicionales que sustituyen materiales contaminantes. Son biodegradables, reutilizables y culturalmente valiosos.
 
          div(titulo="Agroturismo").ms-5
            p.mb-0 Actividad que combina agricultura y turismo, diversificando ingresos y conservando tradiciones bajo un enfoque de educacin ambiental y sostenibilidad. Beneficios: genera empleo, fortalece la identidad campesina, promueve la conservacin, educa sobre alimentos, impulsa el desarrollo local. Ejemplos en Colombia: Huila, zona cafetera, Sierra Nevada de Santa Marta, Cundinamarca.
    
