    #t_3_7.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 3.7 Regulaciones frente al uso de semillas y material gentico
    
    .row.mb-5.align-items-center
      .col-lg-5.order-lg-2.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/85.png", data-aos="zoom-in")
      .col-lg-7.order-lg-1
        .bg-color-6.tarjeta.p-5(data-aos="fade-left")
          p.mb-0 En el contexto de la sostenibilidad rural, el tema de las semillas y el material gentico cobra gran relevancia, ya que se relaciona directamente con la soberana alimentaria, el derecho al trabajo, y la vida digna de las comunidades campesinas, indgenas y afrocolombianas.
    
    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Problemtica actual
    
    p.mb-5 Desde el siglo pasado, con el auge del mejoramiento gentico industrial impulsado por empresas extranjeras, se ha presentado una serie de problemticas que afectan profundamente a los pequeos productores:

    .row.mb-5.justify-content-center
      .col-sm-6.col-lg-4.mb-4.mb-lg-0(data-aos="fade-down")
        .tarjeta--boton.bg-color-24.p-4.h-100
          .row.justify-content-center.mb-3
            .col-7
              figure
                img(src='@/assets/curso/tema3/86.svg', alt='Texto que describa la imagen')
          div.text-center
            H5 Prdida de soberana sobre las semillas
            p Las semillas, que tradicionalmente se seleccionaban, guardaban e intercambiaban libremente, pasaron a ser controladas por corporaciones a travs de patentes y tecnologas protegidas.
      .col-sm-6.col-lg-4.mb-4.mb-lg-0(data-aos="fade-up")
        .tarjeta--boton.bg-color-24.p-4.h-100
          .row.justify-content-center.mb-3
            .col-7
              figure
                img(src='@/assets/curso/tema3/87.svg', alt='Texto que describa la imagen')
          div.text-center
            H5 Dependencia de paquetes tecnolgicos
            p Para mantener sus cultivos, los campesinos comenzaron a depender de semillas hbridas o transgnicas, junto con agroqumicos costosos (fertilizantes, pesticidas, herbicidas).
      .col-sm-6.col-lg-4.mb-4.mb-sm-0(data-aos="fade-down")
        .tarjeta--boton.bg-color-24.p-4.h-100
          .row.justify-content-center.mb-3
            .col-7
              figure
                img(src='@/assets/curso/tema3/88.svg', alt='Texto que describa la imagen')
          div.text-center
            H5 Desvinculacin del equilibrio natural
            p Lo que antes ofreca la naturaleza como la fertilidad del suelo, los abonos orgnicos y las semillas propias hoy ha sido reemplazado por insumos industriales que generan dependencia econmica.
    
    p.mb-5 En consecuencia, muchos campesinos no pueden cultivar si no cuentan con dinero suficiente para adquirir insumos externos, lo que dificulta su autonoma y sostenibilidad.

    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Ausencia de una reglamentacin especfica
    
    p.mb-5 Actualmente en Colombia no existe una reglamentacin clara que proteja el derecho de los campesinos sobre las semillas, especialmente las nativas y criollas. Esto genera tensiones en temas fundamentales como:

    .row.mb-5
      .col-lg-5.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/89.png", data-aos="zoom-in")
      .col-lg-7
        .bg-color-9.tarjeta.p-4.mb-3(data-aos="fade-left")
          div
            ul.lista-ul--color.mb-0
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Seguridad y soberana alimentaria
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Derecho al trabajo rural
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Conservacin de la biodiversidad agrcola
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Acceso equitativo a los recursos genticos

    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Propuesta institucional: hacia una regulacin integral

    .row.mb-4.justify-content-center
      .col-lg-4.mb-3.mb-lg-0.order-lg-2
        figure
          img(src="@/assets/curso/tema3/90.png", data-aos="zoom-in")
      .col-lg-8.order-lg-1
        p Ante esta situacin, se han iniciado esfuerzos para construir una normativa que reconozca y proteja las semillas tradicionales y el papel de las comunidades que las preservan. En este sentido, el Instituto Colombiano Agropecuario (ICA) lidera un proyecto macro denominado: Plan de accin para la conservacin, multiplicacin, uso e intercambio de semillas nativas y criollas.
        .bg-color-15.tarjeta.p-4.mb-3(data-aos="fade-left")
          p Este plan incluido en los materiales complementarios tiene como objetivos principales:
          div
            ul.lista-ul--color.mb-0
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Promover la produccin y uso autnomo de semillas nativas y criollas.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Establecer criterios de calidad y sanidad vegetal sin excluir a los pequeos productores.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Reconocer el rol cultural y tcnico de las comunidades campesinas, indgenas y afrocolombianas.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Fortalecer el intercambio comunitario de semillas como prctica ancestral.
    
    .row.justify-content-center
      .col-lg-4.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema3/91.png", data-aos="zoom-in")
      .col-lg-8
        p La regulacin del uso de semillas y material gentico no puede limitarse a criterios tcnicos o comerciales. Es urgente desarrollar un marco legal que:
        .bg-color-4.tarjeta.p-4.mb-3(data-aos="fade-left")
          div
            ul.lista-ul--color.mb-0
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Reconozca los derechos colectivos sobre las semillas.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Proteja la biodiversidad agrcola.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Fortalezca la soberana alimentaria.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Y asegure que los campesinos puedan seguir cultivando con autonoma, dignidad y justicia.
    
    separador
    
    #t_3_8.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 3.8 La biopiratera y sus riesgos para las comunidades campesinas y la soberana alimentaria
    
    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema3/92.png')})`}"
      )(data-aos="zoom-in")
      .bloque-texto-g__texto.p-4
        p.mb-0 En el contexto de la biodiversidad y los conocimientos tradicionales, la biopiratera representa una amenaza directa para las comunidades rurales, indgenas y afrodescendientes, as como para su autonoma y la soberana alimentaria.
    
    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Qu es la biopiratera?
    
    .row.justify-content-center.mb-5
      .col-lg-4.mb-3.mb-lg-0.order-lg-2
        figure
          img(src="@/assets/curso/tema3/93.png", data-aos="zoom-in")
      .col-lg-8.order-lg-1
        p La biopiratera se refiere al uso, aprovechamiento o comercializacin de recursos biolgicos (como plantas, semillas, animales o microorganismos) y de conocimientos tradicionales asociados (como saberes sobre medicina natural, cultivos o usos de especies nativas) sin el consentimiento ni el beneficio para las comunidades que los han conservado por generaciones. Esta prctica ocurre cuando empresas o investigadores:
        .bg-color-4.tarjeta.p-4.mb-3(data-aos="fade-left")
          div.mb-3
            ul.lista-ul--color.mb-0
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Se apropian de recursos y conocimientos locales.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Los registran como propios mediante patentes u otros mecanismos.
              li.mb-1.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Los explotan comercialmente sin reconocer ni compensar a las comunidades.
          p.mb-0 En esencia, la biopiratera constituye un hurto del patrimonio natural y cultural, especialmente grave en un pas como Colombia, cuya legislacin en este mbito es an dbil, permisiva o inexistente.
    
    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Riesgos de la biopiratera para las comunidades y la soberana alimentaria
    
    p.mb-5 Los impactos de la biopiratera van ms all del mbito legal o econmico; afectan directamente la capacidad de las comunidades rurales para vivir de manera autnoma, cuidar su territorio y preservar sus saberes. A continuacin, se presentan algunos de los riesgos ms importantes:

    TabsC.color-acento-contenido.mb-5
      .p-5(titulo="Prdida del control sobre semillas tradicionales")
        .row.align-items-center
